:root {
  --margin-full:16px;
  --margin-half:8px;
}

* {
  box-sizing:border-box;
}

body {
  font-size:16px;
  font-family:verdana, sans-serif;
  margin:0 auto;
  padding:0 8px;
  padding:0 var(--margin-half);
  background-color:#ffe;
}

#header, #footer, #main, #side {
  /*border:1px solid #ccc;*/
  padding:8px;
  border-radius:4px;
  background-color:#fff;
  box-shadow:2px 2px 8px #bbb;
}

#header {
  margin-top:8px;
  margin-top:var(--margin-half);
  margin-bottom:16px;
  margin-bottom:var(--margin-full);
}

#main {
  margin-bottom:16px;
  margin-bottom:var(--margin-full);
}

#side {
  margin-bottom:16px;
  margin-bottom:var(--margin-full);
}

#footer {
  margin-bottom:8px;
  margin-bottom:var(--margin-half);
}

@media only screen and (max-width:979px) {
  
}

@media only screen and (min-width:980px) {
  body{
    max-width:960px;
  }
  
  #container {
    display:grid;
    grid-template-columns: 1fr 240px;
  }
  
  #header {
    grid-column-start: 1; 
    grid-column-end: 3; 
    grid-row-start: 1; 
    grid-row-end: 2; 
  }
  
  #side {
    margin-left:8px;
  }
  
  #footer {
    grid-column-start: 1; 
    grid-column-end: 3; 
    grid-row-start: 3; 
    grid-row-end: 4; 
  }
}
.header-logo {  width:100px;}#logo-text {  font-size:24px;  margin-bottom:16px;}@media only screen and (max-width:979px) {  .header-logo, #logo-text, #menu {    display:block;    margin:16px auto;    text-align:center;  }}@media only screen and (min-width:980px) {  #header {    display:grid;    grid-template-columns: 100px 1fr auto;    align-items:center;    grid-column-gap:16px;  }    .header-logo {    grid-column-start: 1;     grid-column-end: 2;     grid-row-start: 1;     grid-row-end: 3;  }    #logo-text {    grid-column-start: 2;     grid-column-end: 3;     grid-row-start: 1;     grid-row-end: 2;  }    #menu {    grid-column-start: 2;     grid-column-end: 4;     grid-row-start: 2;     grid-row-end: 3;  }}
#menu .menu_item {
  color:inherit;
  text-decoration:none;
  display:inline-block;
  margin-right:8px;
  padding:4px;
  border:1px solid #ccc;
  border-radius:4px;
  text-align:center;
}

@media only screen and (max-width:979px) {
  #header {
    position:relative;
  }
  
  #menu .menu_item {
    display:block;
    max-width:320px;
    margin:0 auto 8px;
  }
  
  #menu {
    margin-bottom:-8px;
  }
  
  #menu_toggle {
    position:absolute;
    top:8px;
    right:8px;
    padding:8px;
    border:1px solid #ccc;
    border-radius:4px;
    cursor:pointer;
  }
  
  #menu_items_wrapper.menu_mobile_toggled_on {
    display: block;
  }
  #menu_items_wrapper {
    display: none;
  }
}

@media only screen and (min-width:980px) {
  #menu_items_wrapper {
    display: flex;
    flex-flow: row wrap;
    align-content: space-between;
    margin-bottom:-8px;
  }
  #menu_items_wrapper.menu_mobile_toggled_on,
  #menu_items_wrapper.menu_mobile_toggled_off {
    display: flex;
  }
  
  #menu .menu_item {
    flex-grow: 1;
    margin-bottom:8px;
  }
  
  #menu_toggle {
    display:none;
  }
}

#main .image img {
  height:auto;
  max-width:100%;
}

#main .image {
  margin-bottom:8px;
}

#reload-button {
  position:fixed;
  right:24px;
  bottom:24px;
  font-size:32px;
  line-height:0;
  background-color:white;
  border-radius:24px;
  height:48px;
  width:48px;
  padding:8px;
  cursor:pointer;
}

@media only screen and (min-width:480px) {
  #main {
    display:grid;
    grid-template-columns: repeat(2,1fr);
    grid-template-rows:1fr;
    grid-gap: 8px;
  }
  
  #main .image {
    margin:0;
  }
}

@media only screen and (min-width:980px) {
  #main {
    display:grid;
    grid-template-columns: repeat(3,1fr);
    grid-template-rows:1fr;
    grid-gap: 8px;
  }
}
.imagewrapper {
  position:relative;
}

.imagewrapper .magnifier {
  position:absolute;
  right:8px;
  bottom:8px;
  text-align:right;
}

.imagewrapper .magnifier i {
  font-size:50px;
  color:white;
}
#side .static_height_block {
  height:200px;
  display:grid;
  text-align:center;
  border-radius:4px;
  border:1px solid #ccc;
  box-shadow:2px 2px 8px #bbb;
  align-items:center;
}
@media only screen and (min-width:980px) {
  #footer {
    display:grid;
    grid-template-columns: auto auto;
  }
  
  #footer1 {
    justify-self:start;
  }
  
  #footer2 {
    justify-self:end;
  }
}
